/* App styles */

.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-header {
  width: 60vw;
}

/* Grid styles */

.Grid {
  position: relative;
  background-color: #000;
  width: 661px;
  height: 361px;
  background-size: 20px 20px;
  background-image:
    linear-gradient(to right, 	#80F103, 1px, transparent 1px),
    linear-gradient(to bottom, 	#80F103, 1px, transparent 1px);
}

/* Cell styles */

.Cell {
  position: absolute;
  background: #80F103;
}

/* Modal styles */

.ModalButton {
  position: absolute;;
  top:0;
  right:0;
  z-index:300;
}

.ModalStyles {
  max-width: 70vw !important;
}